  handleCommand(command: string): any {
    if (this.isCommission) {
      forEach(this.sorts, item => {
        console.log(item.command)
        if (item.command === command) {
          item.active = true
          this.$forceUpdate()
        } else {
          item.active = false
        }
      })
    } else {
      forEach(this.sortsOther, item => {
        if (item.command === command) {
          item.active = true
          this.$forceUpdate()
        } else {
          item.active = false
        }
      })
    }
    this.$forceUpdate()
    this.$emit('sort', +command)
  }
