









































































































































































































































import FilterSalary from '../components/filter/FilterCommission.vue'
import PopupFilter from '../components/filter/Filter.vue'
import PopupFilterAudit from '@/modules/audit/components/popup/AuditPopupFilter.vue'
import { Component, Mixins } from 'vue-property-decorator'
// import SalaryRepository from '@/services/repositories/salary'
import ContractRepository from '@/services/repositories/contract'
import { PaginationInterface } from '@/interface'
import { forEach, includes, assign } from 'lodash'
import DistrictMixin from '@/mixins/commonMixin'
import PopupMixin from '@/mixins/popup'

import getRepository from '@/services'
import { namespace } from 'vuex-class'
// const salaryReq: SalaryRepository = getRepository('salary')
const beAudit = namespace('beAudit')

@Component({
  components: { FilterSalary, PopupFilter, PopupFilterAudit }
})
export default class SalaryCommission extends Mixins(DistrictMixin, PopupMixin) {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }
  data = []
  contractReq!: ContractRepository
  isLoading = false
  query: PaginationInterface = {
    total: 100,
    page: 1,
    limit: 20,
    searchType: 'CONTRACT',
    contractStatus: null,
    auditStatus: 4
  }

  summary: Record<string, any> = {}

  isExcelLoading = false
  isPdfLoading = false

  created(): void {
    this.contractReq = getRepository('contract', { districtId: this.districtId })
  }

  mounted(): void {
    this.init()
  }

  get getIndex(): number {
    return this.query.limit * (this.query.page - 1) + 1
  }

  get getScreen(): string {
    if (window.screen.availWidth >= 1920) {
      return 'full'
    }
    if (window.screen.availWidth > 1366 && window.screen.availWidth < 1920) {
      return 'hd'
    }
    return 'low'
  }

  async init(): Promise<void> {
    try {
      this.isLoading = true
      //@ts-ignore
      const filters = this.getFilter(this.$route.name)
      if (filters) {
        this.query = assign(this.query, filters.filters)
      }
      const temp = {
        ...this.query,
        total: null
      }

      const result = await this.contractReq.getData(temp)
      this.data = result.data
      const total = await this.contractReq.getContractCount(temp)
      this.query.total = total.data
      const summary = await this.contractReq.getSalaryContract(temp)
      this.summary = summary.data
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
    }
  }

  summaryMethod(params: { columns: any; data: Array<Record<string, any>> }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 9) {
        sums[index] = ''
        return
      }
      if (index === 9) {
        sums[index] = 'Tổng'
        return
      }
      const a = this.summary[column.property] || 0

      // //@ts-ignore
      // sums[index] = a ? this.$options.filters.formatNumber(a) : 0
      sums[index] = `${
        //@ts-ignore
        includes(column.property, 'Percent') || includes(column.property, 'percent') ? this.$options.filters.formatPercent(a) : this.$options.filters.formatNumber(a)
      }`
      sums[14] = ''
      sums[15] = ''
      sums[16] = ''
      // if (this.getScreen === 'full' || this.getScreen === 'hd') {
      //   //@ts-ignore
      //   sums[8] = this.$options.filters.formatPercent(this.summary['salarySalesAmount'] / this.summary['ipAmount'])
      // } else {
      //   sums[8] = ''
      //   //@ts-ignore
      //   sums[9] = this.$options.filters.formatPercent(this.summary['salarySalesAmount'] / this.summary['ipAmount'])
      // }
    })
    console.log('sum', sums)
    return sums
  }

  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }
  handleOpenPopupFilter(): void {
    this.setOpenPopup({
      popupName: 'audit-filter',
      isOpen: true
    })
  }
  handleSort(command: number): void {
    this.query.orderByClause = command
    this.init()
  }
  handleSearch(params: Record<string, any>): void {
    this.query = {
      ...this.query,
      ...params
    }
    this.query.page = 1
    this.init()
  }

  handleFilter(data: Record<string, any>): void {
    const objectKeys = Object.keys(data)
    forEach(objectKeys, elm => {
      if (data[elm]) {
        this.query[elm] = data[elm]
      } else {
        if (this.query[elm]) {
          delete this.query[elm]
        }
      }
    })
    this.query.page = 1
    this.init()
  }
  async handleExport(ext: string): Promise<void> {
    if (this.data.length) {
      if (ext === 'xlsx') {
        this.isExcelLoading = true
      } else {
        this.isPdfLoading = true
      }
      this.contractReq
        .exportReportSummary('salcom', {
          ext: ext,
          ...this.query,
          total: null
        })
        .then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          const date = new Date()
          const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}${date.getHours()}${date.getMinutes()}${date.getMilliseconds()}`
          link.setAttribute('download', `Bao_cao_chi_tiet_hoa_hong_${time}.${ext === 'pdf' ? 'pdf' : 'xlsx'}`)
          document.body.appendChild(link)
          link.click()
          if (ext === 'xlsx') {
            this.isExcelLoading = false
          } else {
            this.isPdfLoading = false
          }
        })
        .catch(err => {
          if (ext === 'xlsx') {
            this.isExcelLoading = false
          } else {
            this.isPdfLoading = false
          }
        })
    } else {
      this.$message.error('Không tìm thấy dữ liệu xuất file')
      this.isExcelLoading = false
      this.isPdfLoading = false
    }
  }
}
